import Vue from 'vue'
import Vuex from 'vuex'
import gql from 'graphql-tag'
Vue.use(Vuex)

const SET_PARTNERS = 'SET_PARTNERS'
const store = new Vuex.Store({
  state: {
    partners: null,
  },
  mutations: {
    [SET_PARTNERS](state, partners) {
      state.partners = partners
    },
  },
  actions: {
    async loadPartners({ commit }) {
      const q = gql`
        {
          partners {
            name
            id
            currency
            country
          }
        }
      `
      try {
        const result = await Vue.helium.q(q, {}, { disableCache: true })
        commit(SET_PARTNERS, result.data.partners)
      } catch (error) {
        //
      }
    },
  },
})
export default store
