import Vue from 'vue'
import App from './App.vue'
import ComponentPlugin from 'rs-components/plugins/components'
import store from './store'
import router from './router/index'
import { helium, api } from './api'

import { AuthPlugin, GraphQLPlugin } from 'rs-components/plugins'

AuthPlugin.onAuthenticated((profile) => {
  store.dispatch('loadPartners')
  // store.dispatch('loadUsers')
  // store.dispatch('setEnabledViews')
})
const authOptions = {
  domain: process.env.VUE_APP_DOMAIN,
  clientId: process.env.VUE_APP_CLIENTID,
  audience: process.env.VUE_APP_AUDIENCE,
}
Vue.use(AuthPlugin, { store, authOptions, apis: [helium, api] })
Vue.use(GraphQLPlugin, { store, api: helium })
Vue.use(ComponentPlugin, { store })

Vue.use(GraphQLPlugin, { store, api: helium })
new Vue({
  store,
  render: (h) => h(App),
}).$mount('#app')
